<template>
    <figure :title="textStatus" style="display: inline-block;">
    <svg class="uptime-status" viewBox="0 0 100 100" style="width: 1.5ex; height: 1.5ex;">
        <circle cx="50" cy="50" r="48" :fill="dotColor"/>
    </svg>
    </figure>
</template>

<script>
export default {
    props: {
        domain: {type: String, required: true},
        refresh: {type: Boolean, required: false},
    },
    data: function() {
        return {
            status: null,
            error: null,
            timer: null,
        };
    },
    computed: {
        dotColor: function() {
            if (this.error)
                return "#999";
            if (!this.status)
                return "#ddd"
            if (this.status.status)
                return "limegreen";
            return "red";
        },
        textStatus: function() {
            let msg = "";
            if (this.status) {
                msg += this.status.status ? "For tiden oppe" : "For tiden nede";
                msg += ' – ' + this.status.ratio + '% oppetid idag';
                msg += '\n(Sist oppdatert: ' + this.status.refreshed.toTimeString().substr(0, 5) + ')';
            }
            else if (!this.error) {
                return "Laster fra oppetid.app.uib.no";
            }
            if (this.error) {
                if (msg) msg += '\n';
                msg += "Feil fra oppetid-serveren: " + this.error;
            }
            return msg;
        },
    },
    created () {
        this.fetchStatus();
        if (this.refresh)
            this.timer = setInterval(() => { this.fetchStatus(); }, (55 + Math.random()*10) * 1000);
    },
    destroyed () {
        if (this.timer)
            clearInterval(this.timer);
    },
    methods: {
        fetchStatus() {
            this.error = null;
            fetch(`https://oppetid.app.uib.no/getStatus.php?return=json&days=1&tjeneste=${this.domain}`, {cache: 'no-store'})
                .then(res => {
                    if (res.ok)
                        return res.json();
                    throw new Error('Response ' + res.status);
                })
                .then(data => {
                    data.refreshed = new Date;
                    this.status = data;
                })
                .catch(err => {
                    this.error = err;
                });
        },
    },
};
</script>