<template>
    <article class="service" v-if="svc">
        <ul class="links">
            <li v-if="svc.link.home"><a :href="svc.link.home" title="Gå til tjenesten"><fa-icon name="external-link"/></a></li>
            <li v-if="svc.link.doc"><a :href="svc.link.doc" title="Dokumentasjon av tjenesten"><fa-icon name="question-circle"/></a></li>
            <li v-if="bus.ita_mode"><a :href="svc.link.href_tk + '/edit'" title="Rediger info om tjenesten (krever innlogging)"><fa-icon name="pencil-square-o"/></a></li>
        </ul>
        <div class="kicker"><fa-icon name='cog'/> Tjeneste </div>
        <h2>{{svc.title}}
            <template v-if="svc.link.domain">
                – <a :href="svc.link.home">{{svc.link.domain}}</a>
                <sup style="font-size: 50%"><uptime-status :domain="svc.link.domain" refresh/></sup>
            </template>
        </h2>
        <p v-if="'summary' in svc.text" class="lead">{{svc.text.summary}}</p>
        <p v-if="'description' in svc.text" class="desc">{{svc.text.description}}</p>

        <div class="fields">
            <field v-if="svc.related && svc.related.service_owner" name="Systemeier">
                {{svc.related.service_owner.title}}
            </field>
            <field v-if="svc.related && svc.related.support_contact" name="Brukerstøtte">
                {{svc.related.support_contact.title}}
            </field>
            <field v-if="bus.ita_mode && svc.info.availability" name="Normalt tilgjengelig">
                {{svc.info.availability_formatted}}
            </field>
            <field v-if="bus.ita_mode" name="Tjenestetype">
                {{svc.info.service_type_formatted}}
            </field>
            <field v-if="bus.ita_mode" name="Kritikalitet">
                {{svc.info.criticality_formatted}}
            </field>
            <field v-if="bus.ita_mode" name="Informasjonsklasse">
                {{svc.info.information_class_formatted}}
            </field>
            <field v-if="bus.ita_mode" name="Livssyklus">
                {{svc.info.service_state_formatted}}
            </field>
            <field v-if="bus.ita_mode" name="Vaktordning">
                {{svc.info.service_rotation_formatted}}
            </field>
            <field v-if="svc.info.service_supplier" name="Driftsleverandør">
                {{svc.info.service_supplier_formatted}}
            </field>
            <field v-if="bus.ita_mode && svc.related && svc.related.op_team" name="Driftsteam">
                {{svc.related.op_team.title}}
            </field>
            <field v-if="bus.ita_mode" name="Registreringsstatus">
                {{svc.info.edit_state_formatted}}
            </field>
        </div>

        <div v-if="bus.ita_mode" class="meta">
            Opprettet {{ svc.meta.created.substr(0,10) }}
            av <user v-bind="svc.meta.author"/>,
            oppdatert {{ svc.meta.changed.substr(0,10) }}.
        </div>
    </article>
    <div v-else><p v-if="err">{{err}}</p></div>
</template>

<script>
import FaIcon from '@/components/FaIcon.vue';
import Field from '@/components/Field.vue';
import UptimeStatus from '@/components/UptimeStatus.vue';
import User from '@/components/User.vue';
import { eventBus } from '@/main';
import tkdata from '@/tkdata';

export default {
    name: 'Service',
    props: {
        svcId: { type: String, required: true }
    },
    data() {
        return {
            svc: null,
            bus: eventBus,
            err: null,
        }
    },
    created() {
        tkdata().then(data => {
            const svc = data.service[this.svcId];
            if (!svc)
                this.err = '404 Tjenesten finnes ikke';
            this.svc = svc;
        });
    },
    components: {
        FaIcon,
        Field,
        UptimeStatus,
        User
    }
}
</script>

<style>
    article.service {
        x-margin: 2ex 0;
    }
    .links {
        float: right;
        font-size: 150%;
        color: #cf3c3a;
    }
    .links li {
        display: inline-block;
        margin-left: 5px;
    }
    .links a {
        opacity: 0.5;
    }
    .links a:hover {
        opacity: 1;
        transition: all 0.5s ease;
    }
    .kicker {
        font-size: 80%;
        color: #cf3c3a;
        text-transform: uppercase;
    }
    article.service h2 {
        font-size: 130%;
    }
    article.service p {
        margin: 1ex 0;
    }
    .lead {
        font-style: italic;
    }
    .desc {
        white-space: pre-line;
        font-size: 80%;
    }
    .fields {
        font-size: 80%;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 10px;
        margin: 2ex 0;
    }
    .field {
        border: 1px dotted gray;
        padding: 12px 5px 5px 5px;
        position: relative;
        background: white;
    }
    .field-name {
        font-size: 60%;
        color: #cf3c3a;
        position: absolute;
        top: 0;
        left: 2px;
    }
    .meta {
        font-size: 60%;
        font-style: italic;
        opaciry: 0.5;
    }

</style>